.sectionGoods {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin: 80px 0;
}

.tittleAndButtons {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
}

.goodsTitle {
  color: var(--Gray-Primary, #0F0F11);
  font-size: 32px;
  font-weight: 800;
  line-height: 41px;
  letter-spacing: -0.32px;
}

.buttonsContainer {
  display: flex;
  flex-direction: row;
  gap: 16px;
}

.buttonLeft {
  width: 32px;
  height: 32px;
  border-radius: 48px;
  transform: rotate(-90deg);
  border: 1px solid var(--gray-icons-placeholders, #B4BDC3);

  &:hover {
    border-radius: 48px;
    border: 1px solid var(--Gray-Primary, #0F0F11);
  }
}

.buttonArrowLeft {
  width: 16px;
  height: 16px;
}

.buttonRight {
  width: 32px;
  height: 32px;
  padding-top: 2px;
  border-radius: 48px;
  border: 1px solid var(--gray-icons-placeholders, #B4BDC3);
  transform: rotate(90deg);

  &:hover {
    border-radius: 48px;
    border: 1px solid var(--Gray-Primary, #0F0F11);
  }
}

.buttonArrowRight {
  width: 16px;
  height: 16px;
}
