.sectionBanner {
  display: flex;
  margin-top: 56px;
}

.bannerButtons {
  width: 32px;
  height: 400px;
  flex-shrink: 0;
  border-radius: 48px;
  border: 1px solid var(--gray-icons-placeholders, #B4BDC3);

  &:hover {
    border: 1px solid var(--Gray-Primary, #0F0F11);
  }
}

.sliderArrowLeft {
  width: 16px;
  height: 16px;
  transform: rotate(-90deg);
}

.sliderArrowRight {
  width: 16px;
  height: 16px;
  transform: rotate(90deg);
}

.bannerImg {
  display: flex;
  width: 1040px;
  height: 400px;
  flex-shrink: 0;
  border-radius: 8px;
  background: lightgray 50% / cover no-repeat;
  margin-inline: 16px;
}

.bannerContent {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.bannerDotters {
  display: flex;
  justify-content: center;
  align-items: center;
}
